import React, { useCallback } from "react"
import { useClerkOrganizationMember } from "@rp/react/common/hooks"
import { UserAvatar } from "@rp/react/web/common/base-components"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Box } from "@rp/react/web/mui/core"
import { SelectItem } from "../../../filesPage.types"

type CustomOwnersSelectorLineProps = SelectItem & { toggle: (item: SelectItem) => void }

function CustomOwnersSelectorLine({ toggle, ...item }: CustomOwnersSelectorLineProps) {
  const { data: orgUser, isLoading } = useClerkOrganizationMember(item.value)
  const handleClick = useCallback(() => toggle(item), [toggle, item])

  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        cursor: "pointer",
        width: "calc(100% - 44px)",
      }}
      onClick={handleClick}
      key={item.value}
    >
      <UserAvatar
        isLoading={isLoading}
        ownerName={item.label}
        size={30}
        imgUrl={orgUser?.publicUserData.imageUrl}
        hasImage={true || orgUser?.publicUserData.hasImage}
      />

      <EllipsisTooltip typographyProps={{ mx: 1 }} tooltipText={item.label}>
        {item.label}
      </EllipsisTooltip>
    </Box>
  )
}

export { CustomOwnersSelectorLine }
